<template>
  <img alt="Vue logo" src="./assets/pic.jpg" width="500"><br>
  <button v-on:click="showData">คลิกเพื่อดูชื่อ</button>
  <button @click="showDataa">คลิกเพื่อดูนามสกุล</button>
  <h3>อายุ : {{age}}</h3>
  <button @click.right="pluss(5)">เพิ่มอายุ+5</button> 
  <button @click.ctrl="minuss(10)">ลดอายุ-10</button> 
<br><br>
  <form>
    <label for="">ป้อนชื่อจริง : </label>
    <input type="text" v-on:input="setfirstname">
    <br><br>
    <label for="">ป้อนนามสกุล : </label>
    <input type="text" v-on:input="setlastname">
    <br><br>
    <label for="">ป้อนชื่อเล่น : </label>
    <input type="text" v-on:input="setnickname">
    <br><br>
    <label for="">ป้อนอายุ : </label>
    <input type="text" v-on:input="setage">
    <br><br>
    <label for="">ป้อนวัน/เดือน/ปีเกิด : </label>
    <input type="text" v-on:input="setbday">
    <br><br>
    <label for="">ป้อนสาขา : </label>
    <input type="text" v-on:input="setmajor">
    <br><br>
    <label for="">ป้อนห้อง : </label>
    <input type="text" v-on:input="setroom">
    <br><br>
    <label for="">ป้อนคติประจำใจ : </label>
    <input type="text" v-on:input="setmotto">
    <br><br>
    <label for="">ป้อนสถานที่เรียนต่อ : </label>
    <input type="text" v-on:input="setnexthell">
    <br><br>
    <label for="">ป้อนวิชาที่ไม่ชอบ : </label>
    <input type="text" v-on:input="setminihell">
    <br><br>
  </form>
  <h2>แสดงชื่อจริง : {{fname}}</h2>
  <h2>แสดงนามสกุล : {{lname}}</h2>
  <h2>แสดงชื่อเล่น : {{nickname}}</h2>
  <h2>แสดงอายุ : {{agee}}</h2>
  <h2>แสดงวัน/เดือน/ปีเกิด : {{bday}}</h2>
  <h2>แสดงสาขา : {{major}}</h2>
  <h2>แสดงห้อง : {{room}}</h2>
  <h2>แสดงคติประจำใจ : {{motto}}</h2>
  <h2>แสดงสถานที่เรียนต่อ : {{nexthell}}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ : {{minihell}}</h2>

</template>

//ใช้right ctrl ในbuttonได้ เพื่อจำกัดการกดปุ่ม , ใช้ @ แทนได้
//สามารถกำหนดจำนวนการบวกลบได้ตามบรรทัดเพิ่มลดอายุ

<script>

export default {
  name: 'App',
  data(){
    return{
      firstname : "sayaphorn",
      lastname : "hlongthong",
      age:19,
      nickname:"",
      fname:"",
      lname:"",
      agee:"",
      bday:"",
      major:"",
      room:"",
      motto:"",
      nexthell:"",
      minihell:"",
    }
  },
  methods:{
    showData(){
      alert(this.firstname)
    },
    showDataa(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    pluss(val){
      this.age+=val
    },
    minus(){
      this.age--
    },
    minuss(val){
      this.age-=val
    },
    setfirstname(event){
      // console.log(event.target.value); -- ไว้เช็คเฉยๆ
      this.fname = event.target.value
    },
    setlastname(event){
      this.lname = event.target.value
    },
    setnickname(event){
      this.nickname = event.target.value
    },
    setage(event){
      this.agee = event.target.value
    },
    setbday(event){
      this.bday = event.target.value
    },
    setmajor(event){
      this.major = event.target.value
    },
    setroom(event){
      this.room = event.target.value
    },
    setmotto(event){
      this.motto = event.target.value
    },
    setnexthell(event){
      this.nexthell = event.target.value
    },
    setminihell(event){
      this.minihell = event.target.value
    },
  }
}
</script>

